.header {
  display: flex;
  height: 70px;
  width: 100%;
  padding: 20px 50px;
  box-sizing: border-box;
  justify-content: space-between;
}

.brand-link {
  text-decoration: none;
  color: #fff;
  font-size: 25px;
  font-weight: 600;
  text-shadow: 1px 1px 1px rgb(0 0 0);
}

.menu-link {
  text-decoration: none;
  color: #fff;
  font-size: 17px;
  font-weight: 600;
  line-height: 30px;
  margin-right: 50px;
  text-shadow: 1px 1px 1px rgb(0 0 0);
}

.menu-link:last-child {
  margin-right: 0;
}

.selected {
  color: #92218f;
  text-shadow: 1px 1px 1px rgb(255 255 255);
}
