.content {
  display: flex;
  height: 70vh;
  width: 100%;
  /* background: linear-gradient(230deg, #b5c0df, #92218f); */
  padding: 10%;
  box-sizing: border-box;
  color: #fff;
}

.portfolio-name {
  font-weight: 600;
  font-size: 4em;
}

.portfolio-tagline {
  font-weight: 600;
  font-size: 1.6em;
}

.portfolio-btn {
  border-radius: 25px;
  margin-top: 2rem;
  padding: 0.7rem 4rem;
  border: 1px solid #92218f;
  background: #92218f;
  color: #fff;
  font-weight: 400;
  box-shadow: 1px 2px 15px rgb(255 255 255 / 20%);
}
